import _ from 'lodash'

export default( state = {}, action) =>{
    switch(action.type){
        case 'FETCH_STREAM':
            return {...state,[action.payload.id]:action.payload};
        case 'CREATE_STREAM':
            return {...state,[action.payload.id]:action.payload};
        case 'EDIT_STREAM':
            return {...state,[action.payload.id]:action.payload};
        case 'DELETE_STREAM':
            return _.omit(state, action.payload)// makes a new object on it's own
        case 'FETCH_STREAMS':
            return { ...state, ..._.mapKeys(action.payload,'id')}
        case 'UPVOTE':
            return {...state,[action.payload.id]:action.payload};
        case 'DOWNVOTE':
            return {...state,[action.payload.id]:action.payload};
        default:
            return state
    }
}


