import React, { Component } from 'react'
import { connect } from 'react-redux'
import {fetchStreams,upvote,downvote} from '../../actions'
import {Link} from 'react-router-dom'
import { Button, Popup } from 'semantic-ui-react'

class StreamList extends Component {

  componentDidMount(){
    this.props.fetchStreams()
    
  }

  renderAdmin(stream){
    if(stream.userId=== this.props.currentUserId){
      return(
        <div className="right floated content">
          <Link className=" ui button primary" to={`/streams/edit/${stream.id}`}>
            Edit
          </Link>
          <Link className=" ui button negative" to={`/streams/delete/${stream.id}`}>
            Delete
          </Link>
        </div>
      )
    }
  }
  renderupdown(stream){

    if(this.props.currentUserId){
      
      return(
        <div className="left floated content  ">
          <button className="ui icon green button bob"id={stream.id} onClick={()=>{
              let re={"rel":stream.rel+1}
              this.props.upvote(stream.id,re)

              
              if ( !document.getElementById(`${stream.id}`).classList.contains('disabled') ){
                document.getElementById(`${stream.id}`).classList.toggle('disabled');
              }  
          }}>
            <i className="arrow alternate circle up icon"> 
            </i>
          </button>
          {stream.rel + ' '}

          <button className="ui icon red button bob"id={stream.id+'r'} onClick={()=>{
              let re={"rel":stream.rel-1}
              this.props.downvote(stream.id,re)
              
              if ( !document.getElementById(`${stream.id+'r'}`).classList.contains('disabled') ){
                document.getElementById(`${stream.id+'r'}`).classList.toggle('disabled');
              }  
          }}>
            <i className=" arrow alternate circle down icon"> 

            </i>
          </button>
        </div>
      )
    }else{
      return(
        <div className="left floated content  ">
              <Popup
        content='Sign in to vote'
        on='click'
        pinned
        trigger={<Button content='Button up'className="ui icon green button bob" children={
          <i className="arrow alternate circle up icon"> 
            </i>
        } />}
      />
          {stream.rel + ' '}
          <Popup
        content='Sign in to vote'
        on='click'
        pinned
        trigger={<Button content='Button down'className="ui icon red button bob" children={
          <i className="arrow alternate circle down icon"> 
            </i>
        } />}
      />
        </div>
      )
    }
  }

  rendername(){
    if(this.props.currentUserId){
       let name= " " + this.props.currentUserId.currentUser

      return(
        
        <div className="bor">
          <img className="ui avatar image spaace" src={this.props.currentUserId.pic}/>    
             {name}    
      </div>
      )
    }
  }

  renderList=()=>{
    return this.props.streams.map(stream =>{
      return(
        <div className='item' key={stream.id}>
          {this.renderupdown(stream)}
          {this.renderAdmin(stream)}
          <i className="large middle aligned icon camera"/>
          <div className="content">
            <Link to={`/streams/${stream.id}`}className="header">{stream.title}

          <div className='description'>
          {stream.description}
          </div>
          </Link>
          </div>
          
          
        </div>
        
      )
    })
  }

  renderCreate(){
    if(this.props.isSignedIn){
      return(
        <div style={{textAlign: 'right'}}>
          <Link to='/streams/new' className="ui button primary down">
            Create post
          </Link>
        </div>
      )
    }
  }
  render() {
    return (
      <div >
        <br/>
        <div className="ui right aligned grid">
        <h4 className="right floated right aligned six wide column pad">
          {this.rendername()}
        </h4>
        {this.renderCreate()}
        </div>
        <h2 className='bobtr'>Blogs:</h2>
        
      <div className='ui celled list'>{this.renderList()}</div>
      
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    streams: Object.values(state.streams),
    currentUserId: state.auth.userId,
    currentUser:state.auth.userId,
    isSignedIn: state.auth.isSignedIn
  };
}

// const mapDispatchToProps = {
  
// }

 export default connect(mapStateToProps, {fetchStreams,upvote,downvote})(StreamList)
