import React from 'react'
// import history from '../../history'
import { fetchStream} from '../../actions';
import { connect } from 'react-redux';


class StreamShow extends React.Component{
  componentDidMount(){
    this.props.fetchStream(this.props.match.params.id)
  }

  comment=()=>{
    if(this.props.currentUserId != null){
      let name= "Author: "+ this.props.currentUserId.currentUser
      console.log(name)
    }
  }

  renderCreate(){
    if(this.props.isSignedIn){
      return(
        <div style={{textAlign: 'right'}}>
          <button className="ui button primary down" onClick={this.comment}>
            Comment
          </button>
        </div>
      )
    }
  }


  render(){
    if(!this.props.stream){
      return 'Loading...'
    }
    
  return (   
      <div>
        <div className="ui right aligned grid">
        <h1>{this.props.stream.title}</h1>
        <h4 className="right floated right aligned six wide column pad"></h4>
          {this.renderCreate()}
          
        </div>
        
        
        <h4>{this.props.stream.description}</h4>
        
      </div>
  )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
     stream: state.streams[ownProps.match.params.id],
     isSignedIn: state.auth.isSignedIn,
     currentUserId:state.auth.userId
    };
};

export default connect (mapStateToProps,{fetchStream })(StreamShow)
