import React from 'react';
import {Link} from 'react-router-dom';
import GoogleAuth from './GoogleAuth'
const Header = () =>{
  return (
    <div className="ui secondary pointing menu">
        <Link to="/" className="item">
            JTB Blogs.com
        </Link>
        <div className="right menu">
        <Link to="/" className="item">
            JTB Blogs
        </Link>
        </div>
        <GoogleAuth/>
           
    </div>
  )
}

export default Header