import React, { Component } from 'react'
import { connect } from 'react-redux'
import {signIn, signOut} from '../actions/'

export class GoogleAuth extends Component {
    
    //gapi client libary javascript refernace
    componentDidMount(){
        window.gapi.load('client:auth2', ()=>{
            window.gapi.client.init({
                //For TESTING IN locaal host change client id to:474701895667-fc0tuc22q8osnfcbrsbgecergi8euc0m.apps.googleusercontent.com
                //1083543663432-rocmmesl0ges09p5n33fofu030ks4leq.apps.googleusercontent.com
                clientId:'1083543663432-rocmmesl0ges09p5n33fofu030ks4leq.apps.googleusercontent.com',
                scope:'email',
                scope:'profile'
            }).then(()=>{
                this.auth = window.gapi.auth2.getAuthInstance();
                // this.setState({isSignedIn: this.auth.isSignedIn.get()})// fiset get value
                // console.log(this.auth.currentUser.get().getBasicProfile().Ad)
                this.onAuthChange(this.auth.isSignedIn.get())
                this.auth.isSignedIn.listen(this.onAuthChange);// call it evry time with inbuilt event handler
            });
        });

    }

    onAuthChange =(isSignedIn) =>{
        if (isSignedIn){
            this.props.signIn(this.auth.currentUser.get().getId(),this.auth.currentUser.get().getBasicProfile().Ad,this.auth.currentUser.get().getBasicProfile().QK);

        }else{
            this.props.signOut();
        }
    }
    onSignInC =()=>{
        this.auth.signIn()
    }
    onSignOutC =()=>{
        this.auth.signOut()
    }
    renderAuthButton(){
        if(this.props.isSignedIn === null){
            return null
        }else if(this.props.isSignedIn){
            return(
                <button className="ui red google button" onClick={this.onSignOutC}>
                    <i className="google icon"/>
                    Sign out
                </button>
            )
        }else{
            return(               
                <button className="ui red google button"onClick={this.onSignInC }>
                    <i className="google icon"/>
                        Sign in with Google
                 </button>
            )
        }
    }

    render() {
        return (
            <div>
                {this.renderAuthButton()}
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {isSignedIn: state.auth.isSignedIn}
}


export default connect(mapStateToProps,{signIn,signOut}) (GoogleAuth)
